.left-side {
    background: #fff ;
    width: 25%;
    margin: 5% 5% 0 5%;
    border: 6px solid lightslategray;
    border-radius: 25px;
}
.left-side p {
    color: #ffffff;
    padding-top: 100px;
    font-size: 35px;
    text-align: center;

}
.left-side img {
    padding: 20px;
    height: 30%;
    width: 55%;
    border-radius: 25px;
}

.left-side-text{
    text-align: start;
    display: flex;
    font-weight: bold;
    word-wrap:break-word;
    font-size: 20px;
}
